@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
body {
  margin: 0;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: goldenrod;
}

li {
  list-style: none;
  padding: 0;
  margin: 0;
}

ul {
  padding: 0;
  margin: 0;
}
.centered {
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
}

.grey {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  opacity: 25%;
}

.number {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.head {
  font-size: 3em;
}

.setup {
  visibility: hidden;
}

.play {
  visibility: hidden;
}

.endgame {
  visibility: hidden;
}

.invisible {
  opacity: 0;
}

.visible {
  opacity: 1;
  position: absolute;
  width: 100vw;
  height: 100vh;
}

.App {
  background-color: goldenrod;
  height: 100vh;
  max-height: 100vh;
  font-family: 'Indie Flower', cursive;
  font-size: calc(3vh);
  display: grid;
}

@media screen and (max-aspect-ratio: 3/4) {
  .App {
    max-height: calc(90vh - 70px);
    grid-template-columns: 1fr 1fr;
    grid-template-rows: calc(45vh - 35px) calc(45vh - 35px);
    background-color: goldenrod;
    font-size: calc(3vw);
  }

  .Backwards {
    grid-row: 2;
    grid-column: 1;
    flex-direction: column-reverse;
    border-top: 0.5em solid black;
    max-height: calc(45vh - 35px);
  }

  .Forwards {
    grid-row: 2;
    grid-column: 2;
    flex-direction: column-reverse;
    border-top: 0.5em solid black;
    max-height: calc(45vh - 35px);
  }

  .Hazards {
    padding-bottom: 1em;
  }

  .Card {
    grid-row: 1;
    grid-column-start: 1;
    grid-column-end: 3;
  }

  .Yeah {
    grid-column: 2;
    grid-row-start: 1;
    grid-row-end: 4;
  }

  .Nah {
    grid-column: 1;
    grid-row-start: 1;
    grid-row-end: 4;
  }

  .mobileHide {
    display: none;
  }
}

@media screen and (min-aspect-ratio: 3/4) {
  .App {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
  }

  .Backwards {
    grid-row: 1;
    grid-column: 1;
    flex-direction: column;
  }

  .Forwards {
    grid-row: 1;
    grid-column: 4;
    flex-direction: column;
  }

  .Card {
    grid-row: 1;
    grid-column-start: 2;
    grid-column-end: 4;
  }

  .Yeah {
    grid-row: 1;
    grid-column-start: 3;
    grid-column-end: 5;
  }

  .Nah {
    grid-row: 1;
    grid-column-start: 1;
    grid-column-end: 3;
  }
}
.Landing {
    z-index: 5;
    color: black;
    grid-row-start: 1;
    grid-row-end: 200;
    grid-column-start: 1;
    grid-column-end: 100;
    display: -webkit-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

h1 {
    font-size: 4em;
    margin: 0;
    padding: 0;
}

input {
    margin: 0.5em;
    padding: 0.5em;
    border-radius: 5vw 15vw 5vw 15vw/15vw 5vw 15vw 5vw;
    border-style: none;
    background: yellow;
    text-align: center;
    font-size: 1em;
    width: 10em;
}

#lobbyMessage {
    font-size: 2em;
    padding: 1em;
}

#enterGame {
    cursor: pointer;
    font-size: 2em;
    margin: 0.75em;
    padding: 0.25em;
    border-radius: 5vw 15vw 5vw 15vw/15vw 5vw 15vw 5vw;
    background-color: gold;
}

#startGame {
    cursor: pointer;
    display: none;
    font-size: 2em;
    margin: 0.75em;
    padding: 0.25em;
    border-radius: 5vw 15vw 5vw 15vw/15vw 5vw 15vw 5vw;
    background-color: green;
}

@media screen and (max-aspect-ratio: 3/2) {
    input {
        font-size: 1.5em;
    }
}
.Player {
    color: black;
    display: -webkit-flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    text-align: center;
    font-weight: bold;
}

.meeple {
    min-height: 1.5em;
    min-width: 1.5em;
    max-width: 10em;
    margin: 0.5em;
    margin-left: 0;
    font-size: 0.75em;
}

@media screen and (max-aspect-ratio: 3/2) {
    .Player {
        font-size: 1.25em;
    }
    
    .meeple {
        font-size: 1em;
    }
}
.Card {
    z-index: 1;
    background-color: beige;
    color: black;
    padding: 1em;
    margin: 1em;
    border-radius: 25vw 2vw 25vw 2vw/2vw 25vw 2vw 25vw;
    border: 0.25em black solid;
    text-align: center;
    display: grid;
    grid-template-rows: 1fr 3fr auto;
    grid-template-columns: 1fr 3fr 1fr;
}

.Value {
    grid-row: 3;
    grid-column: 3;
}

.Title {
    grid-row: 1;
    grid-column: 2;
    font-size: 1.5em;
}

.FlavourText {
    grid-row: 3;
    grid-column-start: 1;
    grid-column-end: 3;
}

#score {
    font-size: 2em;
    grid-row: 1;
    grid-column: 3;
    justify-content: center;
    align-items: center;
}

#stats {
    grid-row: 1;
    grid-column: 1;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

@media screen and (max-aspect-ratio: 3/2) {
    .Card {
        margin: 0;
        padding-top: 2em;
        border: none;
        border-radius: 0;
    }

    .Title {
        font-size: 2em;
    }

    .FlavourText {
        font-size: 1.5em;
    }

    .Value {
        font-size: 1.5em;
    }

    #score {
        font-size: 2em;
    }
}
.Timer {
    z-index: 10;
    color: black;
    display: -webkit-flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    text-align: center;
}
.Backwards {
    z-index: 2;
    color: black;
    min-width: 10vw;
    padding: 1em;
    margin: 1em;
    display: -webkit-flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
}

@media screen and (max-aspect-ratio: 3/2) {
    .Backwards {
        margin: 0;
    }
}
.Bonuses {
    color: black;
    width: 100%;
    display: -webkit-flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    text-align: center;
    flex-wrap: wrap;
}

.bonusSymbol {
    font-size: 1.5em;
}
.Forwards {
    z-index: 2;
    color: black;
    min-width: 10vw;
    padding: 1em;
    margin: 1em;
    display: -webkit-flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
}

.playerList {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

@media screen and (max-aspect-ratio: 3/2) {
    .Forwards {
        margin: 0;
    }

    .playerList {
        display: flex;
    }
}
.Hazards {
    color: black;
    font-size: 1.5em;
    width: 100%;
    display: -webkit-flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    text-align: center;
    flex-wrap: wrap;
}
.Yeah {
    z-index: 3;
    background-color: forestgreen;
    font-size: 5em;
    text-align: center;
}

.Nah {
    z-index: 3;
    background-color: gold;
    font-size: 5em;
    text-align: center;
}
.Endgame {
    z-index: 4;
    color: black;
    grid-row-start: 1;
    grid-row-end: 200;
    grid-column-start: 1;
    grid-column-end: 100;
    display: -webkit-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}
